import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useImmer } from "use-immer";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Collapse from "react-bootstrap/Collapse";

export default function SimpleAccounting() {
    const { register, handleSubmit, reset } = useForm(); //, watch, errors
    const [records, updateRecords] = useImmer([]);
    const [open, setOpen] = useState(false);

    const onSubmit = data => { updateRecords(draft => { draft.push(data) }); reset({d: '...', k: '...', sum: 0}) }; //console.log(data);

 //   console.log(records);

    function processRecords(indicator) {
        let DValues = 0;
        let KValues = 0;
        let processed = records.map(item => {
            if (item.d === indicator) { DValues = DValues + parseFloat(item.sum) }
            if (item.k === indicator) { KValues = KValues + parseFloat(item.sum) }
            return null
        })
        if (indicator === " " || indicator === "" ||
            indicator === " " || indicator === " " ||
            indicator === " " || indicator === "") { return DValues - KValues } else { return KValues - DValues }
    }

    return <div>
        <Container>
            <Row>
                <Col>  {processRecords(" ")}</Col>
                <Col>  {processRecords(" ")}</Col>
            </Row>
            <Row>
                <Col>{" "}</Col>
                <Col>  {processRecords(" ")}</Col>
            </Row>
            <Row>
                <Col> {processRecords("")}</Col>
                <Col>{" "}</Col>
            </Row>
            <Row>
                <Col>  {processRecords(" ")}  </Col>
                <Col>   {processRecords("  ")} </Col>
            </Row>
            <Row>
                <Col>  {processRecords(" ")} </Col>
                <Col>{" "}</Col>
            </Row>
            <Row>
                <Col>  {processRecords(" ")} </Col>
                <Col>   {processRecords("  ")}  </Col>
            </Row>
            <Row>
                <Col> {processRecords("")} </Col>
                <Col>  {processRecords(" ")} </Col>
            </Row>
        </Container>
        <hr />
        <Button
            onClick={() => setOpen(!open)}
            aria-controls="example-collapse-text"
            aria-expanded={open}
            variant="outline-secondary"
            className="mb-3"
        >
            {open ? " " : " "}
        </Button>
        <Collapse in={open}>
            <div id="example-collapse-text">
                <Container>
                    {records.map((row, index) => <Row key={index}>
                        <Col>{row.d}</Col>
                        <Col>{row.k}</Col>
                        <Col>{row.sum}</Col>
                    </Row>)}
                </Container>
            </div>
        </Collapse>
        <hr />
        <form onSubmit={handleSubmit(onSubmit)}>
            {/* <Form> */}
            <Form.Row>
                <Form.Group as={Col} controlId="formStateD">
                    <Form.Label></Form.Label>
                    <Form.Control as="select"  {...register("d")}  required>
                        <option>...</option>
                        <option> </option>
                        <option></option>
                        <option> </option>
                        <option> </option>
                        <option> </option>
                        <option></option>
                        <option> </option>
                        <option> </option>
                        <option>  </option>
                        <option>  </option>
                        <option> </option>
                    </Form.Control>
                </Form.Group>
                <Form.Group as={Col} controlId="formStateK">
                    <Form.Label></Form.Label>
                    <Form.Control as="select" {...register("k")}  required>
                        <option>...</option>
                        <option> </option>
                        <option></option>
                        <option> </option>
                        <option> </option>
                        <option> </option>
                        <option></option>
                        <option> </option>
                        <option> </option>
                        <option>  </option>
                        <option>  </option>
                        <option> </option>
                    </Form.Control>
                </Form.Group>
                <Form.Group as={Col} controlId="formStateSum">
                    <Form.Label></Form.Label>
                    <Form.Control as="input" {...register("sum")}  required />
                </Form.Group>
            </Form.Row>
            {/* </Form> */}
            <Button variant="outline-secondary" type="submit" > </Button>
        </form>
    </div >
}